import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const state = {
  loading: false,
  runTimeInterval: '',
  socials: '',
  websiteInfo: ''
}
const mutations = {
  SET_LOADING: (state, v) => {
    state.loading = v;
  },
  SET_SOCIALS: (state, v) => {
    state.socials = v;
  },
  SET_SITE_INFO: (state, v) => {
    state.websiteInfo = v;
  },
}
const actions = {
  setLoading: ({ commit }, v) => {
    commit('SET_LOADING', v);
  }
}
const getters = {
  loading: state => state.loading,
  runTimeInterval: state => state.runTimeInterval,
  notice: state => state.websiteInfo ? state.websiteInfo.notice : ''
}
export default new Vuex.Store({
  state,
  mutations,
  actions,
  modules: {},
  getters
})
